import styles from "../styles/Account.module.scss"

import logo from "../Images/logo.png"
import image from "../Images/account/image.jpg"
import steam from "../Images/account/steam.svg"

import { NavLink } from "react-router-dom"

function Account() {
  return <div className={styles.login}>
    <div className={styles.left}>
      <div className={styles.top}>
        <img src={logo} />
        <NavLink to="/sunrise/"></NavLink>
        <NavLink to="/sunrise/contacts"></NavLink>
      </div>
      <div className={styles.main}>
        <form>
          <div className={styles.name}>
            <div className={styles.block}>
              <label for="fname"> </label>
              <input type="text" id="fname" placeholder="">
              </input>
            </div>
            <div className={styles.block}>
              <label for="sname"> </label>
              <input type="text" id="sname" placeholder="">
              </input>
            </div>
          </div>
          <div className={styles.block}>
            <label for="mail">   </label>
            <input type="mail" id="mail" placeholder="example@gmail.com">
            </input>
          </div>
          <div className={styles.block}>
            <label for="login"></label>
            <input type="login" id="login" placeholder="username123">
            </input>
          </div>
          <div className={styles.block}>
            <label for="password"></label>
            <input type="password" id="password" placeholder="password_example">
            </input>
          </div>
          <div className={styles.check}>
            <input type="checkbox" id="check"></input>
            <label for="check">   <span> </span></label>
          </div>
        </form>
        <button> </button>
        <button className={styles.steam}>
          <p>  steam</p>
          <img src={steam} />
        </button>
      </div>
    </div>
    <img src={image} />
  </div>
}

export default Account
